import React from 'react';
import SVG from 'react-inlinesvg';

import mobileearth from './mobileearth.svg';
import earth from './earth.svg';
import arrow from './arrow.svg';

import HeaderTitle from '../HeaderTitle';

import Flex from '../../components/Flex';
import Text from '../../components/Text';
import Box from '../../components/Box';
import withResponsive from '../../hoc/withResponsive';
import Circle from '../../components/Circle';

const texts = [
  '文化作為產業升級之動能',
  '成為亞洲品牌和設計服務輸出中心',
  '臺灣的當代生活風格成為行銷全球的文化品牌',
]

const Content = (props) => (
  <Box
    bg="white"
    px="2em"
    py="3em"
    fontSize={['1em', '1.5em', '1.1em', '1.25em', '1.5em']}
    color="darkBlue"
    {...props}
  >
    <Text textAlign="justify">Fresh Taiwan品牌廠商形成國家隊氣勢，在國內建立了文創產業網絡、活絡產品設計上下游產業鏈；在國際經過多年經營，在全球主要市場站穩腳步，建立遍及全球的文創拓銷網絡。</Text>
  </Box>
)

const Desktop = (props) => (
  <Flex mx="10%" pt="2em" {...props}>
    <Box width={2 / 3} mx="1em">
      <SVG src={earth} />
    </Box>
    <Content
      width={1 / 3}
      mx="1em"
      my="auto"
    />
  </Flex>
)

const Mobile = (props) => (
  <Box mx="10%" {...props}>
    <Content mb="1em" />
    <Box mb="1.5em">
      {texts.map((text, index) => (
        <Flex key={index}>
          <Box mr="0.5em">
            <Circle mx="auto" width="2em" bg="milkBlue" color="white">{index + 1}</Circle>
            {index < texts.length - 1 && (
              <Box width="50%" mx="auto"><SVG src={arrow} /></Box>
            )}
          </Box>
          <Text fontSize="1.2em">{text}</Text>
        </Flex>
      ))}
    </Box>
    <SVG src={mobileearth}/>
  </Box>
)

const Flip = ({isMobile, ...props}) => {
  return (
    <Box py="3em" {...props}>
      <HeaderTitle index="5" />
      <Desktop display={['none', null, 'flex']} />
      <Mobile display={['block', null, 'none']} />
    </Box>
  );
};

export default withResponsive(Flip);
