import React from 'react';
import SVG from 'react-inlinesvg';

import Box from '../../components/Box';
import Flex from '../../components/Flex';
import Text from '../../components/Text';
import buyers from './buyers';

const regions = [
  {
    name: '亞洲',
    color: '#6186B6'
  },
  {
    name: '美洲',
    color: '#52BDBE'
  },
  {
    name: '歐洲',
    color: '#9ED7F5'
  }
]

const Buyers = ({ isMobile }) => {
  return (
    <Flex flexWrap="wrap">
      {buyers.map((buyer, index) => (
        <Box
          width={[1 / 3, null, 1 / 6]}
          px={['1em', null, null, '1.5em']}
          borderRight={!isMobile && index < buyers.length - 1 ? '1px solid' : 'unset'}
          borderColor="darkBlue"
          my={isMobile ? '1em' : 'unset'}
          key={index}
        >
          <Box>
            <Text>{buyer.name}</Text>
          </Box>
          <Box>
            <SVG src={buyer.src} />
          </Box>
        </Box>
      ))}
      <Flex
        position="absolute"
        top={['12%', '6%', ' 5%']}
        px={['0.5em', null, '2em']}
        width="100%"
        justifyContent={isMobile ? 'center' : 'flex-end'}
      >
        {regions.map((region, index) => (
          <Flex key={index} ml="0.25em" alignItems="center">
            <Box width="1em" mr="0.25em">
              <Box pt="100%" bg={region.color} />
            </Box>
            <Text>{region.name}</Text>
          </Flex>
        ))}
      </Flex>
    </Flex>
  );
};

export default Buyers;
