import React from 'react';

import Box from '../../components/Box';
import BackgroundImage from '../../components/BackgroundImage';

import wave from './wave.svg';

const Wave = ({ flip, ...props }) => {
  return (
    <Box position="absolute" transform={flip && 'scaleX(-1)'} {...props}>
      <BackgroundImage src={wave} ratio={40.69 / 162.94} />
    </Box>
  );
};

export default Wave;
