import React from 'react';
import SVG from 'react-inlinesvg';

import Box from '../../../components/Box';

import asia from './asia.svg';
import europe from './europe.svg';
import jp from './jp.svg';
import us from './us.svg';

const places = [europe, us, asia, jp];
const { length } = places;

const MobileWorld = (props) => {
  return (
    <Box px="1em" {...props}>
      {places.map((src, i) => (
        <Box key={i}>
          <SVG src={src} />
          {(i + 1) < length && (
            <Box mx="10%" borderBottom="1px solid" borderColor="darkBlue" />
          )}
        </Box>
      ))}
    </Box>
  );
}

export default MobileWorld;
