import React from 'react';

import Title from './Title';
import pages from '../pages';

import Flex from '../../components/Flex';
import Text from '../../components/Text';


const HeaderTitle = ({ lineBreak, index, color, ...props }) => {
  return (
    <Flex
      flexDirection={lineBreak ? 'column' : 'row'}
      position="relative"
      alignItems="center"
      justifyContent="center"
      color="darkBlue"
      mb="2em"
      {...props}
    >
      <Title title={pages[index].label} width={['45%', '25%', '15%']} />
      <Text ml="0.25em" mt="0.25em" fontSize={['1.25em', '2em', '2.5em']} fontWeight="bold" color={color}>{pages[index].name}</Text>
    </Flex>
  );
};

export default HeaderTitle;
