import React from 'react';
import SVG from 'react-inlinesvg';

import zonePictures from './zonePictures';
import MiniTitle from './MiniTitle';
import mobilelogo from './mobilelogo.svg';

import Flex from '../../components/Flex';
import Text from '../../components/Text';
import Box from '../../components/Box';
import BackgroundImage from '../../components/BackgroundImage';

const titleDatas = [
  {
    name: '歐盟',
    per: 30
  },
  {
    name: '北美自由貿易區',
    per: 22
  },
  {
    name: '中國(含港澳)',
    per: 18.7
  },
  {
    name: '日本',
    per: 15.7
  },
  {
    name: '東協經濟共同體',
    per: 7.2
  },
]

const MainRegion = ({isMobile, ...props}) => {
  return (
    <Box {...props}>
      {isMobile ? (
        <SVG src={mobilelogo} />
      ) : (
        <Flex>
          {titleDatas.map((data, index) => (
            <Box key={index} width={1} mx={['0', '0', '0', '0', "0.5em"]}>
              <Flex alignItems="flex-end" justifyContent="center">
                <Box width={['0', '0', 1 / 3, 1 / 3, 2 / 5]} mr={['0', '0', '0.25em', null, "0.5em"]}>
                  <MiniTitle width={1} per={data.per} />
                </Box>
                <Box>
                  <Text textAlign="left" fontSize={['0', '0', '0.8em', '0.9em', "1em"]}>{data.name}</Text>
                </Box>
              </Flex>
              <Box>
                <BackgroundImage src={zonePictures[index]} ratio={271 / 322} />
              </Box>
            </Box>
          ))}
        </Flex>
        )
      }
    </Box>
  );
};

export default MainRegion;
