import React, { PureComponent } from 'react';
import SVG from 'react-inlinesvg';
import reduce from 'lodash/reduce';
import set from 'lodash/set';
import debounce from 'lodash/debounce';
import Waypoint from 'react-waypoint';
import styled from 'styled-components';
import TWEEN from '@tweenjs/tween.js';

import Box from '../../../components/Box';

import images from './images';

const StyledSVG = styled(SVG)`
  .city {
    cursor: pointer;
  }
`;

const order = [
  'london',
  'paris',
  'frankfurt',
  'bangkok',
  'shanghai',
  'tokyo',
  'newyork',
];

const gap = 500;
const duration = 2000;

function animate(time) {
  requestAnimationFrame(animate);
  TWEEN.update(time);
}

class WorldMap extends PureComponent {
  constructor(props) {
    super(props);
    this.animate = debounce(this.animate, duration, { leading: true });
  }
  state = {}

  handleBaseRef = (ref) => {
    this.baseRef = ref;
  }

  handleBaseLoad = () => {
    this.baseRef.querySelectorAll('.city').forEach((ele) => {
      ele.addEventListener('mouseenter', () => this.setState({ city: ele.dataset.name }));
      ele.addEventListener('mouseleave', () => this.setState({ city: null }));
    })
    this.dots = reduce(this.baseRef.querySelectorAll('.dot'), (dots, ele) => set(dots, ele.dataset.name, ele), {});
    requestAnimationFrame(animate);
    this.loaded = true;
  }

  animateDot = (key) => {
    const dot = this.dots[key];
    if (dot) {
      const originCircle = dot.querySelector('circle');
      const newCircle = dot.insertBefore(originCircle.cloneNode(), originCircle);

      var start = { r: newCircle.getAttribute('r'), opacity: 0.8 };
      new TWEEN.Tween(start)
        .to({ r: 45, opacity: 0 }, duration)
        .easing(TWEEN.Easing.Quadratic.Out)
        .onUpdate(({ r, opacity }) => {
          newCircle.setAttribute('r', r);
          newCircle.setAttribute('opacity', opacity);
        })
        .onComplete(() => dot.removeChild(newCircle))
        .start(); // Start the tween immediately.
    }
  }

  animate = () => {
    if (this.leaved) return;
    order.forEach((key, i) => setTimeout(() => this.animateDot(key), gap * i));
    this.timer = setTimeout(this.animate, gap * order.length * 3);
  }

  handleEnter = () => {
    if (this.loaded) {
      if (!this.entered) {
        this.entered = true;
        this.leaved = false;
        this.animate();
      }
    } else {
      setTimeout(this.handleEnter, 100);
    }
  }

  handleLeave = () => {
    if (!this.leaved) {
      this.leaved = true;
      this.entered = false;
      if (this.timer) {
        clearTimeout(this.timer);
      }
    }
  }

  render() {
    const { city } = this.state;
    const citySrc = city && images[city];
    return (
      <Box position="relative" {...this.props}>
        <Waypoint
          onEnter={this.handleEnter}
          onLeave={this.handleLeave}
        />
        <Box innerRef={this.handleBaseRef}>
          <StyledSVG src={images.base} onLoad={this.handleBaseLoad} />
        </Box>
        {city && citySrc && (
          <Box position="absolute" top="0" left="0" right="0" style={{ pointerEvents: 'none' }}>
            <SVG src={citySrc} />
          </Box>
        )}
      </Box>
    );
  }
}

export default WorldMap;
