import React from 'react';

import indexlogo from './indexlogo.svg';
import mobilehomelogo from './mobilehomelogo.svg';
import BackgroundImage from '../../components/BackgroundImage';
import Box from '../../components/Box';
import withResponsive from '../../hoc/withResponsive';

const index = ({isMobile, ...props}) => {
  return (
    <Box height="100%" {...props}>
      <BackgroundImage
        height="100%"
        src={isMobile ? mobilehomelogo : indexlogo}
        ratio={isMobile ? 1052.67 / 641.78 : 1083 / 1924.06}
      />
    </Box>
  );
};

export default withResponsive(index);
