import React, { PureComponent } from 'react';
import Waypoint from 'react-waypoint';
import { List } from 'immutable';
import { Sticky, StickyContainer } from 'react-sticky';

import withResponsive from '../../hoc/withResponsive';
import Box from '../../components/Box';
import sails from './sails';
import SailContent from './SailContent';
import Boat, { boatRatio } from './Boat';

class Waypoints extends PureComponent {
  state = {
    activeStatus: new List(),
  }

  componentDidMount() {
    this.setState({ isClient: typeof window !== 'undefined' })
  }

  handleEnter = (id) => () => this.setState(({ activeStatus }) => ({
    activeStatus: activeStatus.set(id, true)
  }))

  handleLeave = (id) => () => this.setState(({ activeStatus }) => ({
    activeStatus: activeStatus.set(id, false)
  }))

  render() {
    const { activeStatus } = this.state;
    const { isMobile, isClient } = this.props;
    const windowHeight = typeof window === 'undefined' ? 0 : window.innerHeight;
    return (
      <StickyContainer>
        <Box position="relative" py={['2vh', null, '5vh']} zIndex={1}>
          <Sticky
            topOffset={-windowHeight / 2}
            bottomOffset={-windowHeight / 2 - 16 * 5.75 * boatRatio}
            disableCompensation
          >
            {({ style, isSticky, distanceFromBottom, distanceFromTop }) => {
              const isOut = (distanceFromTop + distanceFromBottom) < 0;
              return (
                <Box
                  position="absolute"
                  top={!isOut && 0}
                  bottom={isOut && 0}
                  left="0"
                  width={1}
                  zIndex={1}
                  style={{
                    ...style,
                    top: isSticky ? windowHeight / 2 : null,
                  }}
                >
                  <Boat
                    position="relative"
                    left={['75%', null, '50%']}
                    transform="translateX(-50%)"
                    width="5.75em"
                  />
                </Box>
              );
            }}
          </Sticky>
          {sails.map((sail, id) => (
            <Box key={id} mt="12em" mb={['24em', null, '12em']}>
              <Waypoint
                onEnter={this.handleEnter(id)}
                onLeave={this.handleLeave(id)}
                bottomOffset={windowHeight / 2}
                topOffset={-windowHeight / 2}
              />
              <Box
                position="relative"
                width="1em"
                left={['75%', null, '50%']}
                transform="translateX(-50%)"
              >
                <SailContent
                  {...sail}
                  key={id + String(isClient)}
                  no={id + 1}
                  visible={activeStatus.get(id)}
                  isMobile={isMobile}
                />
                <Box
                  height="1em"
                  bg="darkBlue"
                  borderRadius="50%"
                  position="relative"
                />
              </Box>
            </Box>
          ))}
        </Box>
      </StickyContainer>
    );
  }
}

export default withResponsive(Waypoints);
