import React from 'react';

import HeaderTitle from '../HeaderTitle';

import Waypoints from './Waypoints';
import Box from '../../components/Box';

import Wave from './Wave';

const waves = [
  {
    left: 0,
    top: '20%',
    width: '10%'
  },
  {
    left: '5%',
    top: '14%',
    width: '8%',
    flip: true,
  },
  {
    left: '15%',
    top: '18%',
    width: '8%'
  },
  {
    right: '13%',
    top: '26%',
    width: '10%'
  },
  {
    right: '4%',
    top: '34%',
    width: '10%'
  },
  {
    right: '18%',
    top: '45%',
    width: '10%',
    flip: true
  },
  {
    left: 0,
    top: '60%',
    width: '12%'
  },
  {
    left: '5%',
    top: '52%',
    width: '8%',
    flip: true,
  },
  {
    left: '20%',
    top: '72%',
    width: '10%',
    flip: true,
  },
  {
    right: '10%',
    top: '60%',
    width: '5%',
  },
  {
    right: '5%',
    top: '76%',
    width: '6%',
    flip: true,
  },
  {
    right: '9%',
    top: '86%',
    width: '8%'
  },
];

const Sail = () => {
  return (
    <Box position="relative" py="3em">
      {waves.map((config, index) => <Wave key={index} {...config} />)}
      <Box
        position="absolute"
        left={['75%', null, '50%']}
        top="0"
        bottom="0"
        width="4em"
        transform="translateX(-50%)"
        style={{ backgroundImage: 'linear-gradient(to bottom, transparent, white, white, white, transparent)' }}
      />
      <HeaderTitle index="2" />
      <Waypoints />
    </Box>
  );
};

export default Sail;
