import React from 'react'
import { Element } from 'react-scroll';

import HomePage from '../containers/HomePage'
import Industry from '../containers/Industry'
import Foreword from '../containers/Foreword'
import Sail from '../containers/Sail'
import Territory from '../containers/Territory'
import International from '../containers/International'
import Flip from '../containers/Flip'
import Report from '../containers/Report'

import Box from '../components/Box'
import withLayout from '../hoc/withLayout';
import theme from '../components/ThemeProvider/theme';

const IndexPage = () => (
  <Box height="100vh" pt={theme.headerHeight}>
    <Element style={{ height: '100%' }} name="intro">
      <HomePage />
    </Element>
    <Element name="foreword">
      <Foreword />
    </Element>
    <Element name="industry">
      <Industry />
    </Element>
    <Element name="sail">
      <Sail />
    </Element>
    <Element name="territory">
      <Territory />
    </Element>
    <Element name="international">
      <International />
    </Element>
    <Element name="flip">
      <Flip />
    </Element>
    <Element name="report">
      <Report />
    </Element>
  </Box>
)

export default withLayout(IndexPage)
